<template>
  <div
    class="content"
    :class="answerResult ? 'correct' : 'wrong'"
  >
    <h2 v-if="answerResult"> </h2>
    <h2 v-else>   </h2>
    <button v-if="resLevel" @click="nextAnswerBut"> </button>
    <button v-else @click="nextAnswerBut"> </button>
  </div>

</template>

<script>
  export default {
    props: ['answerResult', 'resLevel'],
    data () {
      return {
      }
    },
    methods: {
      nextAnswerBut(){
        this.$emit('nextAnswer');
      }
    }
  }

</script>




