<template>
  <div id="app">
    <h1>{{h1}}</h1>
    <hr>
    <div class="statusBar">
      <div
        class="statusPersent" :style="{'max-width': this.percent+'%'}"

      ></div>
    </div>

    <transition name="fade" mode="out-in">
      <first-page v-if="appState === 'StartPage'"
                  @start="begin"
      ></first-page>

      <Level    v-else-if="appState === 'Level'"
                :currentLevel="currentLevel"
                :answersCount="levels[currentLevel].answersCount"
                :valueMin="levels[currentLevel].valueMin"
                :valueMax="levels[currentLevel].valueMax"
                @level="appCorrect"
                @addTime="addTimer"

      ></Level>

      <Answer v-else-if="appState === 'Answer'"
              :answerResult="answerResult"
              :resLevel="resLevel"
              @start="begin"
              @nextAnswer="levelAfter"

      ></Answer>

      <ResultLevel v-else-if="appState === 'Result'"
                   :countLevels="levels.length"
                   :currentLevel="currentLevel"
                   :statusInLevel="statusInLevel"
                   @levelAfterEmit="nextLevel"
                   @finalRes="finalRes"
      ></ResultLevel>

      <FinalResult v-else-if="appState === 'FinalResult'"
                   :currentLevel="currentLevel"
                   :status="status"
                   :total="total"
                   @restart="reStart"
      ></FinalResult>
      <!--  v-show -->
      <TimerTime    v-else-if="appState === 'TimerTime'"
                    @finalRes="finalRes"
      ></TimerTime>
    </transition>

  </div>
</template>
<script>
  export default {
    name: 'app',
    data() {
      return {
        h1: ' ',
        currentLevel: 0,          // 
        appState: 'StartPage',    // 
        statAnswers: [],          // 
        answerResult: '',         // 
        answerInTheLevel: 0,      //  
        status: 0,                //  
        statusInLevel: 0,         //    
        percent: 0,               //  
        total: 0,                 //  
        resLevel: true,           //if  
        levels: [                 // 
          {
            answersCount: 2,      //  
            valueMin: 10,         //   
            valueMax: 99,         //   
            questionsCount: 2,    // 
          },
          {
            answersCount: 3,
            valueMin: 10,
            valueMax: 200,
            questionsCount: 3,
          },
          {
            answersCount: 3,
            valueMin: 100,
            valueMax: 500,
            questionsCount: 3,
          },
          {
            answersCount: 2,
            valueMin: 500,
            valueMax: 999,
            questionsCount: 3,
          },
          {
            answersCount: 3,
            valueMin: 100,
            valueMax: 1999,
            questionsCount: 4,
          },
        ]
      }
    },
    methods: {
      begin() {                               //  
        this.appState = 'Level';
        this.currentLevel = 0;
        this.statusInLevel = 0;
      },

      appCorrect(val) {                       //   true/false
        this.answerResult = val.correct;

        this.statAnswers.push(                //   
          {
            id:         this.currentLevel,    // 
            correct:    val.correct,          // 
            numAnswer:  this.answerInTheLevel //   
          }
        );
        this.answerInTheLevel++;              //    
        this.total++;                         //  
        this.appState = 'Answer';             //    
          // 
        this.percent = 100/this.levels[this.currentLevel].questionsCount;
        this.percent = this.percent*this.answerInTheLevel;
          /*            */
        if (this.answerResult) {
          this.statusInLevel++;
        }
      },

      nextLevel(){                        // 
        this.appState = 'Level';
        this.answerInTheLevel = 0;        //   
        this.currentLevel++;              // 
        this.resLevel = true;             //  ( /)
        this.percent = 0;                 //   
        this.statusInLevel = 0;           //    
      },

      levelAfter(){
        this.appState = 'Level';
        if (this.answerInTheLevel === this.levels[this.currentLevel].questionsCount){
          this.appState = 'Result';
          /*     */
        } else if (this.answerInTheLevel === this.levels[this.currentLevel].questionsCount - 1) {     //
          this.resLevel = false;
        }
      },

      finalRes(){                                 // 
        this.appState = 'FinalResult';
        this.resultStatus();
      },

      reStart(){                                  // 
        this.appState = 'StartPage';
        this.total = 0;
        this.answerInTheLevel = 0;
        this.percent = 0;
      },

      addTimer(){                                 //   ( )
        this.appState = 'TimerTime';
      },

      resultStatus(){                             //   ()
        for (let i = 0; i < this.statAnswers.length; i++) {
          if (this.statAnswers[i].correct) {
            this.status++;
          }
        }
      },
    }
  }
</script>


<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 80%;
  margin: 60px auto;
}

h1, h2 {
  font-weight: normal;
  font-size: 42px;
}
.statusBar{
  height: 15px;
  width: 100%;
  border: 1px solid moccasin;
  border-radius: 7px;
  background-color: cornsilk;
  margin: 25px 0;
}
.statusPersent {
  background-color: orange;
  height: 15px;
  border-radius: 7px;
  transition: 1s;
}
.content {
  height: 50vh;
  width: 100%;
  border: 1px solid moccasin;
  border-radius: 7px;
  background-color: cornsilk;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
button {
  display: block;
  padding: 12px 30px;
  background-color: orange;
  color: #fff;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: bold;
  border: none;
  border-radius: 7px;
  transition: 0.3s;
  outline: none;
}
button:hover {
    transform: scale(1.05);
    box-shadow: 4px 7px 7px rgba(0,0,0,0.1);
}
.buttons {
  display: flex;
  justify-content: space-around;
  width: 100%;
}
.fade-enter-active,
.fade-leave-active{
  transition: opacity .3s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}

.wrong {
  background-color: lightcoral;
}
.correct {
  background-color: darkseagreen;
}

</style>



