<template>
  <div class="content">
    <h2> :</h2>
    <h3>   : {{currentLevel + 1}} </h3>
    <h3>  ({{status}}  {{total}})</h3>
    <div class="buttons">
      <button @click="restart">  </button>
    </div>
  </div>

</template>

<script>
  export default {
    props: ['currentLevel', 'status', 'total'],
    data () {
      return {

      }
    },
    methods:{
      restart(){
        this.$emit('restart');
      }

    }
  }

</script>




